import { Paper, Table, TableHead, TableCell, TableBody, TableRow, Accordion, AccordionDetails, AccordionSummary, Typography, Box, Button } from '@mui/material';
import React, { useState } from 'react';
import { resume } from '../helpers/Resume';
import ExpandMoreIcon from '@mui/icons-material/ExpandMore';

const Experience = () => {
    const [expandedAccordions, setExpandedAccordions] = useState<number[]>([0]);
    const allExperienceExpanded = expandedAccordions.length == resume.experience.length;

    const updateAccordion = (index: number) => {
        if (expandedAccordions.includes(index)) {
            setExpandedAccordions(
                expandedAccordions.filter((number) => number !== index)
            );
        }
        else {
            setExpandedAccordions([...expandedAccordions, index]);
        }
    }

    const updateAllAccordions = () => {
        if (allExperienceExpanded) {
            setExpandedAccordions([]);
        } else {
            setExpandedAccordions([...Array(resume.experience.length).keys()]);
        }
    }

    return (
        <React.Fragment>
            <Paper sx={{ padding: '1.5em' }} elevation={0.75}>
                <Table>
                    <TableHead>
                        <TableRow>
                            <TableCell sx={{ display: 'flex' }}>
                                <Typography sx={{ fontWeight: 'bold', fontSize: '2em' }}>Employment</Typography>
                                <Button sx={{ marginLeft: 'auto' }} variant="contained" onClick={updateAllAccordions}>{allExperienceExpanded ? "Collapse" : "Expand"} all</Button>
                            </TableCell>
                        </TableRow>
                    </TableHead>
                    <TableBody>
                        {resume.experience.map((exp, index) => {
                            return (
                                <TableRow>
                                    <TableCell align="left">
                                        <Accordion key={index} onChange={() => updateAccordion(index)} expanded={expandedAccordions.includes(index)}>
                                            <AccordionSummary
                                                expandIcon={<ExpandMoreIcon />}
                                            >
                                                <Typography component="span">{exp.company}</Typography>
                                                <Typography sx={{ fontSize: '0.9em', alignSelf: 'center', marginLeft: 'auto', paddingRight: '1em' }}> {exp.dates}</Typography>
                                            </AccordionSummary>
                                            <AccordionDetails>
                                                <Box><a style={{ fontWeight: 'bold', fontSize: '1.2em' }}>{exp.title}</a> - <a style={{ fontSize: '1.1em' }}>{exp.location}</a></Box>
                                                {exp.responsibilities.map((resp) => {
                                                    return <Box>• {resp}</Box>
                                                })}
                                            </AccordionDetails>
                                        </Accordion>
                                    </TableCell>
                                </TableRow>
                            )
                        })}
                    </TableBody>
                </Table>
            </Paper>
            <br />
            <Paper sx={{ padding: '1.5em' }} elevation={0.75}>
                <Table>
                    <TableHead>
                        <TableRow>
                            <TableCell sx={{ fontWeight: 'bold', fontSize: '2em' }}>Education</TableCell>
                        </TableRow>
                    </TableHead>
                    <TableBody>
                        <TableRow>
                            <TableCell align="left">
                                {resume.education.map((ed) => {
                                    return (
                                        <React.Fragment>
                                            <Box>{ed.dates}</Box>
                                            <Box><a style={{ fontWeight: 'bold' }}>{ed.degree}</a> - {ed.institution}</Box>
                                            <Box>{ed.gpa} GPA</Box>
                                        </React.Fragment>
                                    )
                                })}
                            </TableCell>
                        </TableRow>
                    </TableBody>
                </Table>
                <Table sx={{ marginTop: '2em' }}>
                    <TableHead>
                        <TableRow>
                            <TableCell sx={{ fontWeight: 'bold', fontSize: '2em' }}>Skills and Tech</TableCell>
                        </TableRow>
                    </TableHead>
                    <TableBody>
                        <TableRow>
                            <TableCell align="left">
                                <Box> <a style={{ fontWeight: 'bold' }}>Languages</a>: {resume.skills.programmingLanguages.join(", ")}</Box>
                                <Box> <a style={{ fontWeight: 'bold' }}>Technology</a>: {resume.skills.software.join(", ")}</Box>
                            </TableCell>
                        </TableRow>
                    </TableBody>
                </Table>
            </Paper>
        </React.Fragment>
    );
}

export default Experience;
